import styled from 'styled-components';

const fontBase = `
text-align: left;
font-family: sans-serif;
`;

export const H1 = styled.h1`
  ${fontBase}
`;
export const H2 = styled.h2`
  ${fontBase}
`;
export const H3 = styled.h3`
  ${fontBase}
`;
export const H4 = styled.h4`
  ${fontBase}
`;
export const P = styled.p`
  ${fontBase}
`;


